html, body, #root, .App {
  text-align: center;
  font-family: sans-serif;
  padding: 0em;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center; /* vertical centering */
  align-items: center;     /* horizontal centering */
  text-align: center;
  height: 100%;
  padding: 1em;
  box-sizing: border-box;
}

.question {
  margin: 1em 0;
}

