} */

.button-group {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.8em;
  max-width: 100%;
}

button {
  width: 200px;
  height: 150px;
  border-radius: 50%;
  font-size: 1.5em;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  transition: background-color 0.2s;
}

.circle {
  width: 125px;
  height: 125px;
  border-radius: 50%;
  font-size: 1.2em;
  font-weight: bold;
  color: black;
  border: none;
  margin: 0em;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-sizing: border-box;
  transition: 0.3s;
  white-space: normal;
  line-height: 1.3;
  word-break: break-word;
  text-shadow: 0 0 4px white, 0 0 6px rgba(255,255,255,0.6);
}

.circle.red {
  background: linear-gradient(135deg, #ff4e50, #c3112d);
}

.circle.green {
  background: linear-gradient(135deg, #4CAF50, #2E7D32);
}

.circle.blue {
  background: linear-gradient(135deg, #2196F3, #0D47A1);
}

.circle.selected {
  outline: 5px solid #000000;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
  transform: scale(1.0);
}

.submit-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
}

.submit-button {
  width: 200px;
  height: 80px;
  font-size: 1.5em;
  padding: 0;
  border-radius: 6px;
  background: royalblue;
}

.submit-button:hover:not(:disabled) {
  background: blue;
  /* background: linear-gradient(90deg, #66bb6a, #388e3c); */
  transform: scale(1.02);
}

.submit-button:disabled {
  background: #ccc;
  cursor: not-allowed;
}